@font-face {
  font-family: 'Roboto';
  src: url(../font/Roboto-Regular.ttf);
}

body {
    margin: 10px;
    padding: 0;
  }
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  body {
    background: #eee;
    font-family: 'Roboto', sans-serif;
  }

  .container {
    margin: 100px auto;
    width: 700px;
  }

  .lang-btn {
    margin: 10px auto;
    padding: 10px 20px;
    border: 2px solid red;
    border-radius: 6px;
    background-color: #fff;
  }

  .lang-btn:hover {
    cursor: pointer;
    color: #fff;
    background-color: red;
  }

  .textarea {
    margin: 0 0 5px;
    padding: 5px;
    width: 671px;
    height: 200px;
    font: 1em/1.5 Verdana, Sans-Serif;
    background: #fff;
  }

  .textarea:focus {
    outline: none;
    border: 1px solid rgb(27, 190, 136);
  }

  .keyboard {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  .keyboard li {
    float: left;
    margin: 0 5px 5px 0;
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    background: #fff;
    border: 1px solid #f9f9f9;
  }

  .capslock, .tab, .left-shift {
    clear: left;
  }

  .keyboard .tab, #keyboard {
    width: 70px;
  }

  .keyboard .capslock {
    width: 80px;
  }

  .keyboard .return {
    width: 77px;
  }

  .keyboard .left-shift {
    width: 95px;
  }

  .keyboard .right-shift {
    width: 109px;
  }

  .keyboard .delete {
    width: 100px;
  }

  .lastitem {
    margin-right: 0;
  }

  .uppercase {
    text-transform: uppercase;
  }

  .keyboard .space {
    width: 400px;
  }

  .on {
    display: none;
  }

  .keyboard li:hover {
    position: relative;
    top: 1px;
    left: 1px;
    color: #FFF;
    border-color: #e5e5e5;
    background-color: rgb(7, 204, 72);
    box-shadow: 11px 11px 12px 0px rgba(186, 80, 83, 0.77);
    cursor: pointer;
    transform: translateY(-2px);
  }

  .keyboard li:active {
    transform: translateY(2px);
  }

  .hover {
    position: relative;
    top: 1px;
    left: 1px;
    color: #fff;
    border-color: #e5e5e5;
    background-color: red !important;
    cursor: pointer;
  }